// ÂºïÂÖ•axios
import axios from 'axios'
//ÂºïÂÖ•loadingÁªÑ‰ª∂(‰ª•ÂÆûÈôÖÈ°πÁõÆ‰∏∫ÂáÜ)
import { Loading } from 'element-ui';
// ÂàõÂª∫ÁöÑaxiosÁöÑÂÆûÂàó requests
const requests = axios.create({
    baseURL:"http://localhost:8080", // Âü∫ÂáÜÂú∞ÂùÄ ÂèØÁõ¥Êé•ÈÖçÁΩÆË∑ØÂæÑ,‰πüÂèØ‰ª£ÁêÜË∑ØÂæÑ(‰ª•ÂÆûÈôÖÈ°πÁõÆ‰∏∫ÂáÜ)
    timeout: 10000 // Ë∂ÖÊó∂Êó∂Èó¥
})
// ËØ∑Ê±ÇÊã¶Êà™
// Â£∞ÊòéÂèòÈáèÊé•Êî∂logding
let loadingInstance
requests.interceptors.request.use((config) => {
    // // ËÆæÁΩÆËØ∑Ê±ÇÂ§¥
    // let token = localStorage.getItem('token')
    // config.headers.Authorization = token
    // ËÆæÁΩÆlogdingÈÖçÁΩÆ ËµãÂÄº
    loadingInstance = Loading.service({
        lock: true,
        text: 'ÊãºÂëΩÂä†ËΩΩ‰∏≠...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
    });
    // ËøîÂõûËØ∑Ê±ÇÂ§¥
    return config
}, err => {
   // ËØ∑Ê±ÇÂ§±Ë¥•ËøîÂõûÂ§±Ë¥•‰ø°ÊÅØ
    return Promise.reject(err)
})

// ÂìçÂ∫îÊã¶Êà™
requests.interceptors.response.use((res) => {
    // ÂìçÂ∫îÊàêÂäüÂàôÂÖ≥Èó≠loadingÂä†ËΩΩ
    loadingInstance.close();
    // ËøîÂõûÂìçÂ∫îÊï∞ÊçÆ
    return res
}, err => {
    // ÂìçÂ∫îÂ§±Ë¥•ÂàôÂÖ≥Èó≠loadingÂä†ËΩΩ  
    setTimeout(() => {
        loadingInstance.close();
    }, 1000);
    // ÂìçÂ∫îÂ§±Ë¥•ËøîÂõûÂ§±Ë¥•‰ø°ÊÅØ
    return Promise.reject(err)
})

export default requests